import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, ObjectKeys, downloadCSVFromJson, parseQuery, parseDateMMDDYYYY } from "../../../stores/Utils";

import {SearchInput_Cmpt, SvgIcon_Cmpt, CalendarModalPicker_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";

import "./styles.css"
import {  search_operations, rangeArr,  prepareCompDatatoCvs, filter_operations, validate_comprobantes, getall_operations } from "../services/server_fetching";
import {  ComprobanteItem_Cmpt } from "..";
import { getall_subaccount, getall_account, get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import { useNavigate, useLocation } from "react-router";
import WithScroll from "../../../components/scroll-decorator";
import { OpenModal } from "../../../components/DialogHRM";
import { OpenToast } from "../../../components/Toast";


const CalendarModalPicker = CalendarModalPicker_Cmpt();


const SearchInput = SearchInput_Cmpt();

const ComprobanteItem = ComprobanteItem_Cmpt();
const Svg = SvgIcon_Cmpt();

const LoadingColorSpinner = LoadingColorSpinner_Cmpt();



const coll2list = "coll2list"


const ComprobantesList  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [range, set_range] = useState(128);
	const [inReview, setInReview] = useState(false);
	const [hasError, setHasError] = useState(false);

	const [loading, set_loading] = useState(false);
	const [loadingData, set_loadingData] = useState(false);


	let location = useLocation();
	const navigate = useNavigate();


	useEffect(() => {
		
		setTimeout(() => {
			const searchHash = location.search?.split("?")?.[1];
			const router = parseQuery(searchHash);
			if(router?.cId){
				updStore("search_acc_record",router?.cId)
				search_operations(loadList)
			}
			get_balances_by_type("comp_under_review","comp_under_review",cb_result);
			// getall_operations(cb_result);
		}, 350);
	},[location]);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId());
			getall_account(cb_result)
			getall_subaccount(handle_getall);
			

		}
	});


	const openUpdateView =  () => {
	
	}


	const openAddView =  (v) => {
		navigate({
			pathname: "/entries"
		});
	}

	const handleChange =  (v) => {
		//!loading && set_loading(true);
		!loadingData && set_loadingData(true);
	}


	const handle_search =  (v) => {
		//set_loading(true);
		
		set_range(v=>128)
		search_operations(loadList);
	}


	const loadList =  () => {
		let docdata = {...getStore("comprobante_by_doc")};
		updStore(coll2list,docdata);
		if(getStore("has_filter_review") && inReview){
			updStore(coll2list,filterIsUnderReview(docdata));
		}
		else if(getStore("has_filter_comp_error") && hasError){
			updStore(coll2list,filterHasError(docdata));
		}
		
		cb_result();
	};

	const handle_getall=  (v) => {
		loadList()
		updStore("comprobante_list", null);
		updStore("comprobante_by_doc", null);
		set_loading(false);
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
		set_loadingData(false);
	};

	const loadMore=  () => {
		set_range(v=>v+128)
	}

	const openCalendarModalPicker =  () => {
		downloadCSVFromJson('export_comprobantes_list_'+ gen16CodeId()+'.csv',prepareCompDatatoCvs())
	}
	

	
	const handleFilterInReview=  () => {
		let docdata = {...getStore("comprobante_by_doc")};
		updStore(coll2list,docdata);
		if(getStore("has_filter_review") && !inReview){
			updStore(coll2list,filterIsUnderReview(docdata));
		}
		setInReview(!inReview);
	}

	const handleFilterhasError=  () => {
		let docdata = {...getStore("comprobante_by_doc")};
		updStore(coll2list,docdata);
		
		if(getStore("has_filter_comp_error") && !hasError){
			updStore(coll2list,filterHasError(docdata));
		}
		setHasError(!hasError);
	}


	const confirmCalendar = (v) => {
		set_loadingData(true);
		if(v?.initDate && v?.lastDate ){
			let nv = {...v};
			nv.initDate = nv.initDate -100;
			nv.lastDate = nv.lastDate + 86380000;
			updStore("date_range_comps",nv);
		}
		filter_operations(loadList);
	}
	

    const openCalendar = () => {
        let modal_data = {};
        modal_data["zIndex"] = 950;
        modal_data["height"] = "80vh";
        // data['observeResize']=true;
        modal_data["props"] = { minHeight: "1vh", overlay: true };
        modal_data["content"] = 
			<CalendarModalPicker 
				onConfirm={confirmCalendar} 
				init={getStore("date_range_comps")?.initDate}
				last={getStore("date_range_comps")?.lastDate}
				isRange={true}
			/>
        
        OpenModal(modal_data);
	}
	

	let dateRangeString = getStore("date_range_comps")?.initDate && parseDateMMDDYYYY(getStore("date_range_comps")?.initDate)  + " - " +parseDateMMDDYYYY(getStore("date_range_comps")?.lastDate);


	const validateRes = () => {
		OpenToast({ text: "Verificacion de comprobantes completada", timeout: 3500 });
		get_balances_by_type("comp_under_review","comp_under_review",cb_result);
	}

	const checkCmp = () => {
		validate_comprobantes(validateRes)
	}



	return (

		<div className="">
			<div className="accounts_header">
				<h2>Comprobantes</h2>
				<div className="_dsplFlx ">
					<p className="group_title addBtn purple" onClick={openAddView}>
						Agregar Comprobantes
					</p>
					<p className="group_title addBtn purple" onClick={openCalendarModalPicker}>
						Exportar a CSV
					</p>
					<p className="group_title addBtn purple" onClick={checkCmp}>
						Revisar Comprobantes
					</p>

					
				</div>
				
				<div className="_dsplFlx " style={{marginTop:14}}>
					<SearchInput
						placeholder={"Buscar ...."} 
						width={360}
						obs={set_obs}
						fetchData={handle_search}
						loading={loading}
						change={handleChange}
						callempty={handle_getall}
						keyFlds={"search_acc_record"}
					/>
					<div className="flexSpace"/>
					<div className="_dsplFlx ">
						<div className="total_comprobante_title">
							No de Comprobantes: 
						</div>
						<div className="total_comprobante gray">
							<span>
								{ObjectKeys(getStore(coll2list))?.length}
							</span>
						</div>
					</div>
				</div>
			</div>
			<div className="_dsplFlx " style={{marginBottom:14, maxWidth:500}}>
			
				<div className={"filter_date_btn _dsplFlx"} onClick={openCalendar}>
					<div className="lbl date">
							{dateRangeString || "Fecha"}
					</div>
					<Svg name={"calendar"} color={"#66666"}/>
				</div>
				{getStore("has_filter_review")?
				<div 
					className={"filter_cmp_btn _dsplFlx"} 
					style={{
						"--filter_cmp_btn_color": inReview?"var(--hrm_palette-color-orange-800)":"var(--hrm_palette-success-fg)",
						"--filter_cmp_btn_bck-color":inReview?"var(--hrm_palette-color-orange-100)":"var(--hrm_palette-success-100)",
					}} 
					onClick={handleFilterInReview}
				>
					<div className="lbl">
						{inReview?"En Revision":"Todos"}
					</div>
					<Svg name={inReview?"alert":"success"} color={"#66666"}/>
				</div>
				:null}
				{getStore("has_filter_comp_error")?
				<div 
					className={"filter_cmp_btn _dsplFlx"} 
					style={{
						"--filter_cmp_btn_color": hasError?"var( --hrm_palette-selected-nav-text)":"var(--hrm_palette-success-fg)",
						"--filter_cmp_btn_bck-color":hasError?"var( --hrm_palette-selected-nav-text-100)":"var(--hrm_palette-success-100)",
						marginLeft:12
					}} 
					onClick={handleFilterhasError}
				>
					<div className="lbl">
						{hasError?"Contiene errors":"OK"}
					</div>
					<Svg name={hasError?"alert":"success"} color={"#66666"}/>
				</div>
				:null}
				<div className="flexSpace"/>
			</div>


			<div className="accounts_container " >
				<div style={{height: 55}}>
					<MenuAcc />
				</div>
				{loadingData?
					<>
					<div className="load_animation"/>
					<div className="_dsplFlx "  style={{marginTop:32}}>
						<div className="flexSpace"/>
						<div className="icon_compr _dsplFlx" >
							<div className="lbl_saving">
									Loading ... 
							</div>
							<LoadingColorSpinner stroke={"#646cff"} size={24} strokeW={8}/>
						</div>
						<div className="flexSpace"/>
					</div>
					</>
				:
				<div>
					{getStore(coll2list) && rangeArr(getStore(coll2list),range)?.map(cid=>{
						return (
							<ComprobanteItem 
								data={getStore(coll2list)[cid]} 
								elmId={cid} 
								key={cid}
								openUpdateView={openUpdateView}
								handleRefreshAll={handle_getall}
								inReview={inReview}
								hasError={hasError}
								updRvw={set_obs}
							/>
						)
					})}
					{getStore(coll2list) && ObjectKeys(getStore(coll2list),range).length >range?
					<div className="_dsplFlx " style={{margin: "15px 0 35px"}}>
						<div className="flexSpace"/>
						<p className="group_title addBtn purple" onClick={loadMore}>
							Cargar Mas
						</p>
						<div className="flexSpace"/>
					</div>
					:null}
				</div>
				}
			</div>
			
		</div>
	);
};

export default ComprobantesList;







const MenuAcc = ({}) =>{
	const [float, setFloat] = useState(false);




	const handleScroll = (e) => {
		if(e>295){
			!float && setFloat(true);
		}else{
			float && setFloat(false)
		}
	}


	return (
		<div className={`header_tbl ${float?"_floating":""}`} >
			<div className="_dsplFlx hdr_bar purple">
				<div className="_dsplFlx tr_bar_90">
				
					<div className=" tr_bar_20" >
						Fecha
					</div>
					<div className=" tr_bar_25">
						Comprobante
					</div>
					<div className=" tr_bar_55">
						Descripcion
					</div>
				</div>
				<div className="flexSpace"/>
				<div className="icon tr_bar_5" >
				</div>
			</div>
			<WithScroll scrollhandler={handleScroll} />
		</div>
	)
}




function filterIsUnderReview(arr1) {
    var newArr = [];
	ObjectKeys(arr1).map((s) => {
		let itm = arr1[s];
		if (getStore("comp_under_review")?.inReview?.[itm?.comprobanteId]) {
			newArr.push(itm);
		}
	})
	return newArr;
}

function filterHasError(arr1) {
    var newArr = [];
	ObjectKeys(arr1).map((s) => {
		let itm = arr1[s];
		if (getStore("comp_under_review")?.hasError?.[itm?.comprobanteId]) {
			newArr.push(itm);
		}
	})
	return newArr;
}